import { Inline, InlineProperties } from '@ali/4ever-cangjie';
import { ELEMENT_TYPE } from '../../utils/meta';

export interface InlinePlaceholderProperties {
  data?: InlineProperties['data'];
  nodes?: InlineProperties['nodes'];
}

export default class InlinePlaceholder extends Inline {
  static TYPE = ELEMENT_TYPE;

  static create(properties: InlinePlaceholder | InlinePlaceholderProperties | string) {
    if (typeof properties === 'string') {
      throw new Error('`InlinePlaceholder.create` does not accept string');
    }
    if (InlinePlaceholder.isInlinePlaceholder(properties)) {
      return properties;
    }
    return super.create({
      ...properties,
      type: InlinePlaceholder.TYPE,
    }) as InlinePlaceholder;
  }

  static isInlinePlaceholder(maybeInlinePlaceholder: any): maybeInlinePlaceholder is InlinePlaceholder {
    return Inline.isInline(maybeInlinePlaceholder) && maybeInlinePlaceholder.type === InlinePlaceholder.TYPE;
  }
}
